/* STRUCTURE */

.wrapper {
  display: flex;
  align-items: center;
  flex-direction: column; 
  justify-content: center;
  width: auto;
  min-height: 50%;
  padding: 30px;
 
}

.add-doctor-wrapper {
  display: flex;
  align-items: center;
  flex-direction: column; 
  justify-content: flex-end;
  width: 100%;
  min-height: 100%;
  padding:20px;
}

#formContent {
  border-radius: 10px 10px 10px 10px;
  background: azure;
  padding: 30px;
  width: 90%;
  max-width: 550px;
  position: relative;
  padding: 0px;
  box-shadow: 0 30px 60px 0 rgba(0,0,0,0.3);
  text-align: center;
}

#add-doctor-formContent
{
  border-radius: 10px 10px 10px 10px;
  background: azure;
  padding:30px;
  width: 100%;
  max-width: 550px;
  position: relative;
  padding: 30px;
  box-shadow: 0 30px 60px 0 rgba(0,0,0,0.3);
  /* text-align: center; */
}

#formFooter {
  background-color: #f6f6f6;
  border-top: 1px solid #dce8f1;
  padding: 25px;
  text-align: center;
  border-radius: 0 0 10px 10px;
}

#gender{
  color:  #757575
}

/* TABS */

.title.inactive {
  color: #cccccc;
}

.title.active {
  color: #0d0d0d;
  border-bottom: 2px solid #5fbae9;
}



/* FORM TYPOGRAPHY*/

.customBtn  {
  background-color: #56baed;
  border: none;
  color: white;
  padding: 15px 80px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  text-transform: uppercase;
  font-size: 14px;
  box-shadow: 0 10px 30px 0 rgba(95,186,233,0.4);
  border-radius: 5px 5px 5px 5px;
  margin: 5px 20px 40px 20px;
  transition: all 0.3s ease-in-out;
}

.customBtn:hover {
  background-color: #278dc0;
}

.customBtn:active {
  -webkit-transform: scale(0.95);
  transform: scale(0.95);
}

.username, .password {
  background-color: #ebdcdc;
  border: none;
  color: #0d0d0d;
  padding: 15px 32px;
  /* text-align: center; */
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  margin: 5px;
  width: 85%;
  border: 2px solid #f6f6f6;
  transition: all 0.5s ease-in-out;
  border-radius: 5px 5px 5px 5px;
}

.username:focus, .password:focus {
  background-color: #fff;
  border-bottom: 2px solid #5fbae9;
}

.username:placeholder, .password:placeholder {
  color: #cccccc;
}


/* OTHERS */

*:focus {
    outline: none;
} 

#icon {
  width:60%;
}

* {
  box-sizing: border-box;
}

