.footer {   /*rgb(44, 6, 6) */
    /* background-color: rgb(213, 218, 218); */
    /* background-color:  rgb(172, 177, 176); */
    /* background-color: #1F51FF; */
 /* background-color: 	#554949;  */
 background-color: rgb(34, 46, 58);			
     /* background-color: #0075b3;  */
    /*background-color:rgb(41, 17, 17);*/
    display: flex;
    flex-direction: column;
    margin-bottom: 0%;
    font-weight: bold;
    color:thistle;
  }
  
  .footer-social-media {
   
    display: flex;
    flex-direction: row;
    justify-content: center;
    margin-bottom: 20px;
  }
  
  .footer-social-media__icon {
    margin: 0px 4px;
    width: 25px;
    height: 25px;
  }
  
  .footer-info  {
    font-family: emoji;
    color: white !important;
    display: flex;
    justify-content: center;
  }
  
  .footer-info-left, .footer-info-center, .footer-info-right {
    font-size: medium;
    display: flex;
    flex-direction: column;
    margin-bottom: 5px;
    width: 30%;
  }
  
  .footer-info__name, .footer-info__email, .footer-info__number {
    margin-bottom: 10px;
    text-shadow: 2px 2px 5px salmon;
  }
  
  .footer-info__name, .footer-info__returns {
    text-align: start;
    
  }
  
  .footer-info__email, .footer-info__terms {
    text-align: center;
  }
  
  .footer-info__number, .footer-info__contact {
    text-align: end;
  }
  
  .footer-date {
    display: flex;
    flex-direction: row;
    align-items: center;
  }
  
  .footer-seperator {
    flex-grow: 1;
    border: none;
    height: 1px;
    background-color: white;
    width: 100%;
    
  }
  
  .footer-name {
    display: flex;
    justify-content: center;
  }
  
  .footer-bottom__name {
    text-transform: uppercase;
    font-family: RobotoBold;
    font-size: large ;
    margin: 0px 20px;
  }
   
  .color{
    color: white;
  }